.food-item{
    width: 100%;
    margin:auto;
    border-radius: 15px;
    box-shadow: 0px 0px 10px #00000015;
    transition: 0.3s;
    animation: fadeIn 1s;
}

.food-image{
    width:100%;
    border-radius: 10px 10px 0px 0px;
}
.food-item-info{
    padding:20px;
}

.food-item-name-rating{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.food-item-name-rating p{
    font-size:22px;
    font-weight: 500;
}

.food-item-name-rating img{
    width:70px;
}

.food-des{
    color:#676767;
    font-size: 12px;
    
}

.food-price{
    font-size: 24px;
    font-weight: 500;
    margin:10px 0px;
    color:tomato;
}

.food-item-img-contaner{
    position:relative;

}

.food-item-img-contaner .add{
    width:35px;
    position:absolute;
    bottom: 15px;
    right: 15px;
    cursor:pointer;
    border-radius: 50px;
}
.food-item-count{

    
    position:absolute;
    bottom: 15px;
    right: 15px;
    display:flex;
    align-items:center;
    gap:10px;
    padding:6px;
    background-color: white;;
    cursor:pointer;
    border-radius: 50px;

}
.food-item-count img{
    width: 30px;
}